<template>
  <a-form-model
    ref="form"
    :model="entity"
    layout="vertical"
    autocomplete="off"
    :rules="rules"
    class="h-father"
  >
    <g-body-form-layout>
      <!-- 搜索插槽 -->
      <template #search-card>
        <div class="search-card">
          <a-row :gutter="24">
            <a-col :span="4">
              <!-- 设备组 -->
              <a-form-model-item label="设备组" prop="deviceGroupName">
                <g-select
                  v-model="entity.deviceGroupName"
                  placeholder="请选择设备组"
                >
                  <!-- 使用options, 动态加载 -->
                  <a-select-option
                    v-for="item in deviceGroupOptions"
                    :key="item.value"
                    :value="item.value"
                  >{{ item.label }}</a-select-option>
                </g-select>
              </a-form-model-item>
            </a-col>
            <!-- 设备 -->
            <a-col :span="4">
              <a-form-model-item label="设备" prop="deviceName">
                <g-select
                  v-model="entity.deviceName"
                  placeholder="请选择设备"
                >
                  <!-- 根据设备组选择加载设备 -->
                  <a-select-option
                    v-for="item in deviceOptions"
                    :key="item.value"
                    :value="item.value"
                  >{{ item.label }}</a-select-option>
                </g-select>
              </a-form-model-item>
            </a-col>
             <a-col :span="8"> </a-col>
              <a-col :span="4">
                <div
                  style="display: flex;
                  justify-content: flex-end;
                  align-items: center;
                  height: 100%;"
                >
                  <g-btn-group
                    :buttonLists="btnList"
                    @btnCallback="btnCallback($event)"
                  ></g-btn-group>
                </div>
              </a-col>
          </a-row>
        </div>
      </template>
      <!-- 展开插槽 -->
      <template #expand-card>
        <div class="expand-card">
          <g-collapse headerName="状态切换配置">
            <template #content>
              <a-row :gutter="24">
                <a-col :span="6">
                  <!-- 当前状态 -->
                  <a-form-model-item label="当前状态" prop="currentStatus">
                    <a-select
                      v-model="entity.currentStatus"
                      placeholder="请选择当前状态"
                      disabled
                    >
                       <a-select-option
                        v-for="item in statusOptions"
                        :key="item.value"
                        :value="item.value"
                      >{{ item.label }}</a-select-option>
                    </a-select>
                  </a-form-model-item>
                </a-col>
                <a-col :span="6">
                  <!-- 目标状态 -->
                  <a-form-model-item label="目标状态" prop="targetStatus">
                    <a-select
                      v-model="entity.targetStatus"
                      placeholder="请选择目标状态"
                      @change="handleStatusChange"
                    >
                      <a-select-option
                        v-for="item in statusOptions"
                        :key="item.value"
                        :value="item.value"
                      >{{ item.label }}</a-select-option>
                    </a-select>
                  </a-form-model-item>
                </a-col>
                <a-col :span="6">
                  <!-- 切换原因 -->
                  <a-form-model-item
                    label="切换原因"
                    prop="reason"
                    :rules="reasonRules"
                  >
                    <a-select
                      v-model="entity.reason"
                      placeholder="请选择切换原因"
                    >
                      <a-select-option value="计划内">计划内</a-select-option>
                      <a-select-option value="计划外">计划外</a-select-option>
                      <a-select-option value="故障">故障</a-select-option>
                      <a-select-option value="其他">其他</a-select-option>
                    </a-select>
                  </a-form-model-item>
                </a-col>
                <a-col :span="6">
                  <!-- 故障代码 (可选) -->
                  <a-form-model-item
                    label="故障代码"
                    prop="faultCode"
                    v-if="entity.reason === '故障'"
                  >
                    <a-input
                      v-model="entity.faultCode"
                      placeholder="请输入故障代码"
                    />
                  </a-form-model-item>
                </a-col>
              </a-row>
            </template>
          </g-collapse>
        </div>
      </template>
      <template #card-body>
          <g-collapse headerName="设备信息">
          <template #content>
            <a-row :gutter="24">
                <!-- 其他设备信息 -->
                <a-col :span="8">
                   <a-form-model-item label="设备类型" prop="deviceType">
                    <a-select
                      v-model="entity.deviceType"
                      placeholder="请选择设备类型"
                      disabled
                    >
                      <a-select-option value="温度传感器">温度传感器</a-select-option>
                      <a-select-option value="湿度传感器">湿度传感器</a-select-option>
                      <a-select-option value="压力传感器">压力传感器</a-select-option>
                      <a-select-option value="振动传感器">振动传感器</a-select-option>
                      <a-select-option value="位移传感器">位移传感器</a-select-option>
                    </a-select>
                  </a-form-model-item>
                </a-col>
              </a-row>
          </template>
        </g-collapse>

        <g-collapse headerName="操作描述">
          <template #content>
            <a-row :gutter="24">
              <a-col :span="24">
                <a-form-model-item label="备注" prop="remark">
                  <a-textarea
                    v-model="entity.remark"
                    placeholder="请输入备注信息"
                    :rows="4"
                  />
                </a-form-model-item>
              </a-col>
            </a-row>
          </template>
        </g-collapse>
      </template>
       <template #g-fixed-row>
        <div class="g-fixed-row">
          <g-btn size="large" text="刷新" @callback="refresh()"></g-btn>
          <g-btn size="large" text="提交" type="primary" @callback="submitStatusChange()">
          </g-btn>
        </div>
      </template>
    </g-body-form-layout>
  </a-form-model>
</template>

<script>
import GSignModal from "@/components/g-sign-modal/g-sign-modal.vue";
import { createModal } from "@/utils";
import AppConst from '@/utils/appConst';

export default {
  name: "DeviceStatusChange",

  data() {
    return {
      entity: {
        deviceGroupName: "",
        deviceName: "",
        currentStatus: "待机", // 默认状态
        targetStatus: "",
        reason: "",
        faultCode: "",
        remark: "",
        deviceType:"",
      },
      //校验规则
      rules: {
        deviceName: [
          { required: true, message: "请选择设备", trigger: "change" },
        ],
        deviceGroupName: [
            {required: true, message: "请选择设备组", trigger: "change"}
        ],
        targetStatus: [
          { required: true, message: "请选择目标状态", trigger: "change" },
        ],
        reason: [
          { required: true, message: "请选择切换原因", trigger: "change" },
        ],
      },
      reasonRules: [
        { required: true, message: "请选择切换原因", trigger: "change" }
      ],
       statusOptions: [
        { value: "运行", label: "运行" },
        { value: "待机", label: "待机" },
        { value: "故障", label: "故障" },
        { value: "维护", label: "维护" },
        { value: "离线", label: "离线" },
        { value: "设置", label: "设置" },
        { value: "测试", label: "测试" },
        {value: "清洁", label: "清洁"},
        {value: "换型", label: "换型"}
      ],
      deviceGroupOptions:[], //设备组
      deviceOptions:[],     //设备列表
       btnList: [
          {
            icon: AppConst.icon.documentShow,
            text: AppConst.button.documentShow,
            eventName: AppConst.button.documentShow,
            display: true
          }
        ],
    };
  },

  watch: {
     "entity.deviceGroupName": {
      handler(newVal) {
        //设备组变化，重新加载设备列表
        if (newVal) {
          this.loadDeviceOptions(newVal);
        } else {
            this.deviceOptions = [] //清空
        }
      }
    },
  },

  mounted() {
    // 加载设备组
    this.loadDeviceGroupOptions();
    //假设默认选中第一个
    if (this.deviceGroupOptions.length > 0){
        this.entity.deviceGroupName = this.deviceGroupOptions[0].value;
    }
  },

  methods: {
    // 提交状态切换
    submitStatusChange() {
      this.$refs.form.validate().then((valid) => {
        if (valid) {
            // 验证通过
            // 1. 签名
            this.createSignModal();
        }
      });
    },
      // 创建签名弹窗
    createSignModal() {
      createModal({
        title: '签名',
        width: '600px',
      },{
        component: GSignModal,
        success: (v) => {
          console.log(v);
          // 2. 提交到后端，这里简化为log
          console.log("提交设备状态切换:", this.entity);
           // 3. 处理提交成功后的逻辑
          this.$message.success('设备状态切换提交成功');
           //4. 刷新
          this.refresh();
        },
      },);
    },
    // 刷新
    refresh() {
      this.$refs.form.resetFields();
      this.entity.currentStatus = "待机"; // 重置为默认状态
       //假设默认选中第一个
        if (this.deviceGroupOptions.length > 0){
            this.entity.deviceGroupName = this.deviceGroupOptions[0].value;
        }
    },
    //状态切换处理
    handleStatusChange(value){
        // 如果切换到故障，则清空故障代码。 如果从故障切换到其他，也清空
        if(value === '故障' || this.entity.currentStatus === '故障'){
             this.entity.faultCode = '';
        }
    },

    // 加载设备组选项(模拟)
    loadDeviceGroupOptions() {
       // 实际中这里应该通过API从后端获取数据
      setTimeout(() => {
        this.deviceGroupOptions = [
          { value: "group1", label: "设备组1" },
          { value: "group2", label: "设备组2" },
          { value: "group3", label: "设备组3" },
        ];
      }, 500);
    },

    // 加载设备选项(模拟)
    loadDeviceOptions(deviceGroupName) {
        //实际中根据设备组 从后台获取
        console.log("选择的设备组:", deviceGroupName)
      setTimeout(() => {
        if (deviceGroupName === "group1") {
          this.deviceOptions = [
            { value: "device1", label: "设备1" },
            { value: "device2", label: "设备2" },
          ];
        } else if (deviceGroupName === "group2") {
          this.deviceOptions = [
            { value: "device3", label: "设备3" },
            { value: "device4", label: "设备4" },
          ];
        } else {
          this.deviceOptions = [];
        }
      }, 500);
    },
     btnCallback(e) {
      console.log(e);
    },
  },
};
</script>