<template>
  <div class="modal">
    <div class="modal-content">
      <div class="sign-container">
        <h3 class="sign-title">{{ title || '请在下方区域签名' }}</h3>
        <g-sign
          ref="signBoard"
          :line-width="lineWidth"
          :line-color="lineColor"
          :background-color="backgroundColor"
          :save-format="saveFormat"
          @save="handleSave"
          @clear="handleClear"
          @save-error="handleError"
        />
      </div>
    </div>
    <div class="modal-footer">
      <g-btn text="确认" @callback="save" type="primary"></g-btn>
      <g-btn text="取消" @callback="$emit('closeModal')"></g-btn>
    </div>
  </div>
</template>

<script>
import GSign from '@/components/g-sign/g-sign.vue';

export default {
  name: "GSignModal",
  components: {
    GSign
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    lineWidth: {
      type: Number,
      default: 2
    },
    lineColor: {
      type: String,
      default: '#000000'
    },
    backgroundColor: {
      type: String,
      default: '#ffffff'
    },
    saveFormat: {
      type: String,
      default: 'png',
      validator: (value) => ['png', 'jpeg', 'webp'].includes(value)
    }
  },
  data() {
    return {
      signatureData: null,
      hasSignature: false
    };
  },
  methods: {
    handleSave(imageData) {
      this.signatureData = imageData;
      this.hasSignature = true;
    },
    
    handleClear() {
      this.signatureData = null;
      this.hasSignature = false;
    },
    
    handleError(message) {
      this.$message.error(message);
    },
    
    save() {
      if (!this.hasSignature) {
        this.$message.error('请先进行签名');
        return;
      }
      
      this.$emit('success', this.signatureData);
      this.$emit('closeModal');
    },
    
    clearSignature() {
      if (this.$refs.signBoard) {
        this.$refs.signBoard.clearCanvas();
      }
    }
  }
};
</script>

